import { Component, OnInit, Input } from '@angular/core';
import { AlertService, twentyFourHoursActivityService } from 'src/app/_services';

@Component({
  selector: 'app-slips-per-client-table',
  templateUrl: './slips-per-client-table.component.html',
  styleUrls: ['./slips-per-client-table.component.scss']
})
export class SlipsPerClientTableComponent implements OnInit {
  @Input() pageSize: number;
  // loading flag
  isLoading: boolean = false;
  // paging
  pageInfo: any = {
      pageNumber: 1,
      pageSize: (!this.pageSize || this.pageSize <= 0) ? 50 : this.pageSize
  };
  // list of slips
  slips: any = [];

  constructor(
    private alertService: AlertService,
    private services:twentyFourHoursActivityService
  ) { }

  ngOnInit() {
    this.getSlips();
  }

  getSlips(): void {
    this.pageInfo.pageNumber = 1;
    this.isLoading = true;
    this.services.getSlipsPerClient().pipe().subscribe(
      data => {
        this.slips = data;
        this.isLoading = false;
      },
      error => {
        this.alertService.error(error, false);
        this.isLoading = false;
      }
    )
  }

}
