import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { AppRoutingModule } from './app-routing.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { LoginComponent } from './pages/authentication/login/login.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { FooterComponent } from './components/footer/footer.component';
import { APP_BASE_HREF } from '@angular/common';
import { AnonymousComponent } from './layouts/anonymous/anonymous.component';
import { AuthComponent } from './layouts/auth/auth.component';
import { LiveDashboardComponent } from './pages/live-dashboard/live-dashboard.component';
import { ErrorLogsComponent } from './pages/twenty-four-hours-dashboard/error-logs/error-logs.component';
import { TwentyFourHoursDashboardComponent } from './pages/twenty-four-hours-dashboard/twenty-four-hours-dashboard.component';
import { PeakRequestsComponent } from './pages/twenty-four-hours-dashboard/peak-requests/peak-requests.component';
import { ProcessTimeDetailsComponent } from './pages/twenty-four-hours-dashboard/process-time-details/process-time-details.component';
import { ResponseTimeVsThroughputComponent } from './pages/twenty-four-hours-dashboard/response-time-vs-throughput/response-time-vs-throughput.component';
import { RequestAndSlipSaveThroughputComponent } from './pages/twenty-four-hours-dashboard/request-and-slip-save-throughput/request-and-slip-save-throughput.component';
import { SlipsPerClientComponent } from './pages/twenty-four-hours-dashboard/slips-per-client/slips-per-client.component';
import { ErrorLogsTableComponent } from './components/error-logs-table/error-logs-table.component';
import { AlertComponent } from './components/alert/alert.component';


// Services
import { AlertService, twentyFourHoursActivityService } from './_services';
import { SlipsPerClientTableComponent } from './components/slips-per-client-table/slips-per-client-table.component';
import { ProcessTimeDetailsTableComponent } from './components/process-time-details-table/process-time-details-table.component';
import { PeakRequestsChartComponent } from './components/peak-requests-chart/peak-requests-chart.component';
import { ResponseTimeVsThroughputChartComponent } from './components/response-time-vs-throughput-chart/response-time-vs-throughput-chart.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    NavbarComponent,
    SidebarComponent,
    FooterComponent,
    AnonymousComponent,
    AuthComponent,
    LiveDashboardComponent,
    TwentyFourHoursDashboardComponent,
    ErrorLogsComponent,
    PeakRequestsComponent,
    ProcessTimeDetailsComponent,
    ResponseTimeVsThroughputComponent,
    RequestAndSlipSaveThroughputComponent,
    SlipsPerClientComponent,
    ErrorLogsTableComponent,
    AlertComponent,
    SlipsPerClientTableComponent,
    ProcessTimeDetailsTableComponent,
    PeakRequestsChartComponent,
    ResponseTimeVsThroughputChartComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    AppRoutingModule,
    NgbModule.forRoot()
  ],
  providers: [
    { provide: APP_BASE_HREF, useValue: '/' },
    AlertService,
    twentyFourHoursActivityService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
