import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-slips-per-client',
  templateUrl: './slips-per-client.component.html',
  styleUrls: ['./slips-per-client.component.scss']
})
export class SlipsPerClientComponent implements OnInit {
  data: any = [];

  // loading flag
  isLoading: boolean = true;
  // paging
  pageInfo: any = {
      pageNumber: 1,
      pageSize: 20
  };

  constructor() { }

  ngOnInit() {
  }

  onDataChanged(data: any){
    this.data = data;
    this.isLoading = false;
  }
}
