import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import * as Highcharts from 'highcharts';
import { AlertService, twentyFourHoursActivityService } from 'src/app/_services';
@Component({
  selector: 'app-response-time-vs-throughput-chart',
  templateUrl: './response-time-vs-throughput-chart.component.html',
  styleUrls: ['./response-time-vs-throughput-chart.component.scss']
})
export class ResponseTimeVsThroughputChartComponent implements OnInit {
  // loading flag
  isLoading: boolean = false;
  // paging
  pageInfo: any = {
    pageNumber: 1,
    pageSize: 10
  };
  // list of requests
  responses: any = [];
  // list of AverageMsPriceResponse
  responseTime: any = [];
  // list of TotalPriceRequests
  requests: any = [];
  // event emitter
  @Output()
  onDataChanged: EventEmitter<number> = new EventEmitter<any>();

  constructor(
    private alertService: AlertService,
    private services: twentyFourHoursActivityService
  ) { }

  ngOnInit() {
    this.getResponseTimes();
  }

  getResponseTimes(): void {
    this.pageInfo.pageNumber = 1;
    this.isLoading = true;

    this.responses = [];
    this.responseTime = [];
    this.requests = [];
    this.services.getResponseTimeVsThroughput().pipe().subscribe(
      data => {
        this.onDataChanged.emit(data);
        this.responses = data;
        this.isLoading = false;
        this.prepareData();
      },
      error => {
        this.alertService.error(error, false);
        this.isLoading = false;
      }
    )
  }

  prepareData() {
    this.responses.forEach((item) => {
      var mArray = item.Minute.split(' ');
      var dateArray = mArray[0].split('-');
      var timeArray = mArray[1].split(':');

      var minute = Date.UTC(parseInt(dateArray[0]), parseInt(dateArray[1]), parseInt(dateArray[2]), parseInt(timeArray[0]), parseInt(timeArray[1]));
      this.responseTime.push([minute, parseInt(item.AverageMsPriceResponse)]);
      this.requests.push([minute, parseInt(item.TotalPriceRequests)]);
    });
    this.initChart();
  }

  initChart() {
    var options: any = {
      chart: {
        type: 'area',
        zoomType: 'x',
        height: '500px',
      },
      title: {
        text: 'Response time vs Throughput'
      },
      xAxis: {
        type: 'datetime',
        title: {
          text: 'Hours'
        }
      },
      yAxis: {
        title: {
          text: ''
        },
        min: 0
      },
      // tooltip: {
      //   headerFormat: '<b>{series.name}</b><br>',
      //   pointFormat: '{point.x:%e. %b}: {point.y:.2f} m'
      // },

      plotOptions: {
        // spline: {
        //   marker: {
        //     enabled: true
        //   }
        // }
        area: {
          
          marker: {
            radius: 2
          },
          lineWidth: 1,
          states: {
            hover: {
              lineWidth: 1
            }
          },
          threshold: null
        }
      },
      series: [
        {
          name: 'Response Time (ms)',
          fillColor: {
            linearGradient: {
              x1: 0,
              y1: 0,
              x2: 0,
              y2: 1
            },
            stops: [
              [0, '#ffffff'],
              [1, '#90caf9']
            ]
          },
          data: this.responseTime
        }, {
          name: 'Requests per min',
          fillColor: {
            linearGradient: {
              x1: 0,
              y1: 0,
              x2: 0,
              y2: 1
            },
            stops: [
              [0, '#ffffff'],
              [1, '#343a40']
            ]
          },
          data: this.requests
        }
      ]
    };


    Highcharts.chart('response-time-vs-throughput-chart', options);

  }
}
