import { Component, OnInit, Input } from '@angular/core';
import { AlertService, twentyFourHoursActivityService } from 'src/app/_services';

@Component({
  selector: 'app-process-time-details-table',
  templateUrl: './process-time-details-table.component.html',
  styleUrls: ['./process-time-details-table.component.scss']
})
export class ProcessTimeDetailsTableComponent implements OnInit {
  @Input() pageSize: number;
  // loading flag
  isLoading: boolean = false;
  // paging
  pageInfo: any = {
    pageNumber: 1,
    pageSize: (!this.pageSize || this.pageSize <= 0) ? 50 : this.pageSize
  };
  // list of details
  details: any = [];

  constructor(
    private alertService: AlertService,
    private services: twentyFourHoursActivityService
  ) { }

  ngOnInit() {
    this.getProcessTimeDetails();
  }

  getProcessTimeDetails(): void {
    this.pageInfo.pageNumber = 1;
    this.isLoading = true;
    this.services.getProcessTimeDetails().pipe().subscribe(
      data => {
        this.details = data;
        this.isLoading = false;
      },
      error => {
        this.alertService.error(error, false);
        this.isLoading = false;
      }
    )
  }
}
