import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Config } from "../_shared/config";

@Injectable({ providedIn: 'root' })
export class twentyFourHoursActivityService {
    constructor(private http: HttpClient) {
    }

    /**
     * Get error logs
    */
    getErrorLogs() {
        return this.http.get(`${Config.HOST_URL}/api/DevOps/24HoursActivity/GetErrorLogs?code=${Config.HOST_KEY}`)
        .pipe(map(datas => {
            return datas['Value'];
        }));
    }

    /**
     * Get Peak Requests
    */
    getPeakRequests() {
        return this.http.get(`${Config.HOST_URL}/api/DevOps/24HoursActivity/GetPeakRequests?code=${Config.HOST_KEY}`)
        .pipe(map(datas => {
            return datas['Value'];
        }));
    }

    /**
     * Get Process Time Details
    */
    getProcessTimeDetails() {
        return this.http.get(`${Config.HOST_URL}/api/DevOps/24HoursActivity/GetProcessTimeDetails?code=${Config.HOST_KEY}`)
        .pipe(map(datas => {
            return datas['Value'];
        }));
    }

    /**
     * Get Request Slip Save Throughput
    */
    getRequestSlipSaveThroughput() {
        return this.http.get(`${Config.HOST_URL}/api/DevOps/24HoursActivity/GetRequestSlipSaveThroughput?code=${Config.HOST_KEY}`)
        .pipe(map(datas => {
            return datas['Value'];
        }));
    }

    /**
     * Get Response Time Vs Throughput
    */
    getResponseTimeVsThroughput() {
        return this.http.get(`${Config.HOST_URL}/api/DevOps/24HoursActivity/GetResponseTimeVsThroughput?code=${Config.HOST_KEY}`)
        .pipe(map(datas => {
            return datas['Value'];
        }));
    }

    /**
     * Get slips per client
    */
    getSlipsPerClient() {
        return this.http.get(`${Config.HOST_URL}/api/DevOps/24HoursActivity/GetSlipsPerClient?code=${Config.HOST_KEY}`)
        .pipe(map(datas => {
            return datas['Value'];
        }));
    }

}