import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { AlertService, twentyFourHoursActivityService } from 'src/app/_services';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'app-peak-requests-chart',
  templateUrl: './peak-requests-chart.component.html',
  styleUrls: ['./peak-requests-chart.component.scss']
})
export class PeakRequestsChartComponent implements OnInit {
  // loading flag
  isLoading: boolean = false;
  // paging
  pageInfo: any = {
    pageNumber: 1,
    pageSize: 10
  };
  // list of requests
  requests: any = [];
  // list of clients
  clients: any = [];
  // list of slipCount
  slipCount: any = [];
  // list of priceRequestCount
  priceRequestCount: any = [];
  // chart object
  chart: any = null;

  // event emitter
  @Output()
  onDataChanged: EventEmitter<number> = new EventEmitter<any>();
  
  constructor(
    private alertService: AlertService,
    private services: twentyFourHoursActivityService
  ) { }

  ngOnInit() {
    this.getPeakRequests();
  }

  getPeakRequests(): void {
    this.pageInfo.pageNumber = 1;
    this.isLoading = true;

    this.requests = [];
    this.clients = [];
    this.slipCount = [];
    this.priceRequestCount = [];
    this.services.getPeakRequests().pipe().subscribe(
      data => {
        this.onDataChanged.emit(data);
        this.requests = data;
        this.isLoading = false;
        this.prepareData();
      },
      error => {
        this.alertService.error(error, false);
        this.isLoading = false;
      }
    )
  }

  prepareData(){
      this.requests.forEach((item) => {
        this.clients.push(item.Client);
        this.slipCount.push(parseInt(item.SlipCount));
        this.priceRequestCount.push(parseInt(item.PriceRequestCount));
    });
    this.initChart();
  }

  initChart(){
    var options: any = {
      chart: {
        type: 'column',
        zoomType: 'y',
        inverted: false,
        height: '500px'
      },
      title: {
        text: 'Peak Requests'
      },
      xAxis: {
        categories: this.clients,
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
          text: ''
        },
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0
        }
      },
      colors:['#f9a226', '#007bff'],
      series: [{
        name: 'Slip Saves (per min)',
        data: this.slipCount    
      }, {
        name: 'Price Request (per sec)',
        data: this.priceRequestCount
      }]
    };


    Highcharts.chart('peak-request-chart', options);

  }

}
