import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-peak-requests',
  templateUrl: './peak-requests.component.html',
  styleUrls: ['./peak-requests.component.scss']
})
export class PeakRequestsComponent implements OnInit {
  data: any = [];

  // loading flag
  isLoading: boolean = true;
  // paging
  pageInfo: any = {
      pageNumber: 1,
      pageSize: 20
  };

  constructor() { }

  ngOnInit() {
  }

  onDataChanged(data: any){
    this.data = data;
    this.isLoading = false;
  }

}
