import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AnonymousComponent } from './layouts/anonymous/anonymous.component';
import { LoginComponent } from './pages/authentication/login/login.component';
import { AuthComponent } from './layouts/auth/auth.component';
import { LiveDashboardComponent } from './pages/live-dashboard/live-dashboard.component';
import { ErrorLogsComponent } from './pages/twenty-four-hours-dashboard/error-logs/error-logs.component';
import { PeakRequestsComponent } from './pages/twenty-four-hours-dashboard/peak-requests/peak-requests.component';
import { ProcessTimeDetailsComponent } from './pages/twenty-four-hours-dashboard/process-time-details/process-time-details.component';
import { ResponseTimeVsThroughputComponent } from './pages/twenty-four-hours-dashboard/response-time-vs-throughput/response-time-vs-throughput.component';
import { RequestAndSlipSaveThroughputComponent } from './pages/twenty-four-hours-dashboard/request-and-slip-save-throughput/request-and-slip-save-throughput.component';
import { SlipsPerClientComponent } from './pages/twenty-four-hours-dashboard/slips-per-client/slips-per-client.component';
import { TwentyFourHoursDashboardComponent } from './pages/twenty-four-hours-dashboard/twenty-four-hours-dashboard.component';


const routes: Routes = [
    { path: 'login', component: AnonymousComponent, children: [{ path: '', component: LoginComponent }] },
    { path: '', redirectTo: '/24h-activity-dashboard', pathMatch: 'full' },
    {
        path: 'live-activity-dashboard',
        component: AuthComponent,
        data: { title: '', breadcrumb: 'Live Dashboard', type: 'dropdown', icon: 'mdi mdi-chart-timeline'},
        children: [{ path: '', component: LiveDashboardComponent }]
    },
    {
      path: '24h-activity-dashboard',
      component: AuthComponent,
      data: { title: '24h Activity Dashboard', breadcrumb: '24h Activity Dashboard', type: 'dropdown', icon: 'mdi mdi-chart-line'},
      children: [
        { 
          path: '', 
          component: TwentyFourHoursDashboardComponent,
          data: { title: '24h Activity Dashboard', breadcrumb: '24h Activity Dashboard', type: 'link', icon: 'mdi mdi-chart-line' }
        },
        { 
          path: 'error-logs', 
          component: ErrorLogsComponent,
          data: { title: 'Error Logs', breadcrumb: 'Error Logs', type: 'link', icon: '' }
        },
        { 
          path: 'peak-requests', 
          component: PeakRequestsComponent,
          data: { title: 'Peak Request', breadcrumb: 'Peak Request', type: 'link', icon: '' }
        },
        { 
          path: 'process-time-details', 
          component: ProcessTimeDetailsComponent,
          data: { title: 'Process Time Details', breadcrumb: 'Process Time Details', type: 'link', icon: '' }
        },
        { 
          path: 'response-time-vs-throughput', 
          component: ResponseTimeVsThroughputComponent,
          data: { title: 'Response Time Vs Throughput', breadcrumb: 'Response Time Vs Throughput', type: 'link', icon: '' }
        },
        { 
          path: 'request-and-slip-save-throughput', 
          component: RequestAndSlipSaveThroughputComponent,
          data: { title: 'Request And Slip Save Throughput', breadcrumb: 'Request And Slip Save Throughput', type: 'link', icon: '' }
        },
        { 
          path: 'slips-per-client', 
          component: SlipsPerClientComponent,
          data: { title: 'Slips Per Client', breadcrumb: 'Slips Per Client', type: 'link', icon: '' }
        }
      ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
