import { Component, OnInit, Input } from '@angular/core';
import { twentyFourHoursActivityService } from 'src/app/_services/twenty-four-hours-activity.service';
import { AlertService } from 'src/app/_services';

@Component({
  selector: 'app-error-logs-table',
  templateUrl: './error-logs-table.component.html',
  styleUrls: ['./error-logs-table.component.scss']
})
export class ErrorLogsTableComponent implements OnInit {
  @Input() pageSize: number;
  // loading flag
  isLoading: boolean = false;
  // paging
  pageInfo: any = {
      pageNumber: 1,
      pageSize: (!this.pageSize || this.pageSize <= 0) ? 50 : this.pageSize
  };
  // list of errors
  errors: any = [];

  constructor(
    private alertService: AlertService,
    private services:twentyFourHoursActivityService
  ) { }

  ngOnInit() {
    this.getErrorLogs();
  }

  getErrorLogs(): void {
    this.pageInfo.pageNumber = 1;
    this.isLoading = true;
    this.services.getErrorLogs().pipe().subscribe(
      data => {
        this.errors = data;
        this.isLoading = false;
      },
      error => {
        this.alertService.error(error, false);
        this.isLoading = false;
      }
    )
  }
}
